* {
  box-sizing: border-box;
}

html {
  color-scheme: light dark;
  --bg-color: #f0f0f0;
  --text-color: #000;
  --toggle-content: "☀️";
  font-family: system-ui;

  &[data-theme="light"] {
    --bg-color: #f0f0f0;
    --text-color: #000;
    --toggle-content: "☀️";
  }

  &[data-theme="dark"] {
    --bg-color: #000;
    --text-color: #f0f0f0;
    --toggle-content: "🌙";
  }

  & body {
    background: var(--bg-color);
    color: var(--text-color);
  }

  & #theme-toggle {
    position: fixed;
    top: 5px;
    right: 5px;

    &:after {
      content: var(--toggle-content);
    }
  }
}

.App {
  --df-green: #10f898;
  --df-quality-common: #c4c4c2;
  --df-quality-uncommon: #2aca96;
  --df-quality-rare: #589fdc;
  --df-quality-epic: #9a71dc;
  --df-quality-legendary: #d0814d;
  --df-quality-exotic: #da5758;
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;

  & .sections {
    flex-flow: wrap;
    justify-content: center;
    gap: 2em;
    padding-top: 40px;
    display: flex;

    & .section {
      min-width: 300px;

      @media (width <= 800px) {
        width: 100%;
      }

      & > .header {
        flex-flow: row;
        justify-content: space-between;
        display: flex;
      }

      & h2 {
        margin: 0 0 .5em;
        padding: 0;
        font-size: 24px;
      }
    }
  }

  & .MapTimers {
    flex-flow: column;
    display: flex;

    & .currentTime {
      margin-bottom: 1em;
      font-size: 18px;
      font-weight: bold;
    }

    & .timers {
      margin-bottom: 1em;

      & .timer {
        border-left: 5px solid var(--df-green);
        padding: .5em;

        & h3 {
          margin: 0;
          padding: 0;
          font-size: 24px;
        }

        &.live {
          background: var(--df-green);
          color: #000;
          font-weight: bold;
        }

        & .time {
          font-size: 18px;
        }
      }
    }

    & .timeline {
      grid-template-rows: [header-start] 1fr[header-end maps-start] auto[maps-end];
      grid-template-columns: [name-start] 1fr[name-end hours-start] repeat(24, auto)[hours-end];
      align-items: center;
      gap: 2px;
      display: grid;

      & .hour-key {
        justify-content: center;
        align-items: center;
        width: 10px;
        height: 10px;
        font-size: 8px;
        display: flex;

        &.live {
          color: var(--df-green);
          font-weight: bold;
        }
      }

      & .name {
        text-align: right;
        max-height: 10px;
        margin-right: .5em;
        font-size: 12px;
        transform: translateY(-4px);
      }

      & .hour {
        content: "";
        background: gray;
        width: 10px;
        height: 10px;
        display: block;

        &.live {
          background: var(--df-green);
        }
      }
    }
  }

  & .buttonGroup {
    flex-flow: row;
    gap: 4px;
    display: flex;
    overflow: hidden;
  }

  & .button {
    cursor: pointer;
    color: #000;
    user-select: none;
    background: var(--df-green);
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    font-size: 32px;
    font-weight: bold;
    display: flex;

    &.add {
      background: var(--df-green);
    }

    &.remove {
      background: var(--df-quality-exotic);
    }

    & > span {
      transform: translateY(-4px);
    }

    &.disabled {
      cursor: not-allowed;
      background: var(--df-quality-common);
    }
  }
}

.Collector {
  & .missions {
    grid-template-columns: auto auto;
    gap: 1em;
    display: grid;

    @media (width <= 800px) {
      grid-template-columns: auto;
    }

    & .mission {
      &.complete {
        & .header {
          background: var(--df-green);
          color: #000;
          padding-left: 5px;
        }
      }

      &.minimised {
        & .header {
          & h3 {
            margin: 0;
          }
        }
      }

      & .header {
        flex-flow: row;
        justify-content: space-between;
        gap: 1em;
        margin-bottom: .5em;
        display: flex;

        & h3 {
          margin: 0;
          padding: 0;
          font-size: 18px;
          font-weight: bold;
        }
      }

      & .items {
        & .item {
          --item-color: transparent;
          border-left: 4px solid #0000;
          border-color: var(--item-color);
          width: 100%;
          box-shadow: inset 100px 0 100px -100px var(--item-color);
          flex-flow: row;
          gap: 1em;
          margin-bottom: 4px;
          padding: 2px 0 2px .5em;
          display: flex;

          &.common {
            --item-color: var(--df-quality-common);
          }

          &.uncommon {
            --item-color: var(--df-quality-uncommon);
          }

          &.rare {
            --item-color: var(--df-quality-rare);
          }

          &.epic {
            --item-color: var(--df-quality-epic);
          }

          &.legendary {
            --item-color: var(--df-quality-legendary);
          }

          &.exotic {
            --item-color: var(--df-quality-exotic);
          }

          & .name {
            flex-flow: row;
            flex-grow: 1;
            gap: 5px;
            display: flex;
          }

          &.complete {
            & .progress {
              color: #000;
              background: var(--df-green);
              padding: 0 4px;
              font-weight: bold;
            }
          }
        }
      }
    }
  }

  & .newThisSeason {
    color: gold;
    font-size: 12px;
  }

  & .requiresExtraction {
    color: #0ff;
    font-size: 12px;
  }

  & .legend {
    margin-top: 2em;

    & > div {
      flex-flow: row;
      gap: .5em;
      display: flex;
    }
  }
}

[data-theme="light"] {
  & .Collector {
    & .newThisSeason {
      color: #ff4500;
    }

    & .requiresExtraction {
      color: #8a2be2;
    }
  }
}

.kofiContainer {
  --df-green: #10f898;
  position: fixed;
  bottom: 5px;
  right: 5px;

  & .link {
    color: #fff;
    flex-flow: row;
    justify-content: center;
    align-items: center;
    gap: .5em;
    padding: 0 5px;
    text-decoration: none;
    display: flex;
    overflow: hidden;

    &:active, &:visited {
      color: #fff;
    }

    &:hover {
      background: var(--df-green);
      color: #000;

      & span {
        background: none;
      }
    }

    & span {
      background: #000;
      border-radius: 4px;
      padding: 2px 12px;
    }
  }
}

.MissionTree {
  flex-flow: column;
  align-items: center;
  gap: 4px;
  width: 100%;
  display: flex;

  & .missionTreeCanvas {
    width: 100%;
    max-width: 2000px;
    height: 200px;
  }

  & .missionDisplay {
    border: 1px solid gray;
    border-top: 5px solid #d3d3d3;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    gap: .5em;
    min-width: 400px;
    max-width: 800px;
    padding: 1em;
    display: flex;

    &.completed {
      border-color: var(--df-green);
    }

    & .name {
      margin-bottom: .5em;
      font-weight: bold;
    }

    & .description {
      & .condition {
        color: red;
        background: #f003;
        margin-bottom: .5em;
        padding: .5em;
      }

      & .quality-indicator {
        width: .75em;
        height: .75em;
        margin: 0 .1em 0 .25em;
        display: inline-block;

        &.green {
          background: var(--df-quality-uncommon);
        }

        &.blue {
          background: var(--df-quality-rare);
        }

        &.purple {
          background: var(--df-quality-epic);
        }

        &.gold {
          background: var(--df-quality-legendary);
        }

        &.red {
          background: var(--df-quality-exotic);
        }
      }

      & ul {
        margin: 0;

        & li {
          margin-bottom: .5em;
          list-style-type: square;
        }
      }

      & em {
        color: gold;
        font-style: normal;
        font-weight: bold;
      }

      & strong {
        color: gold;
      }
    }

    & .status {
      &.completed {
        color: var(--df-green);
      }
    }
  }

  & .missionStats {
    &.complete {
      background-color: var(--df-green);
      color: #000;
      padding: 5px;
      font-weight: bold;
    }
  }
}
